body {
  box-sizing: border-box;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  margin: 0 auto;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
img {
  max-width: 100%;
  height: auto;
}
:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}
::after,
::before {
  box-sizing: border-box;
}
.page {
  background-color: #fff;
}
.page-header {
  outline: 1px dotted coral;
}
.page-footer {
  outline: 1px dotted magenta;
}
.header {
  background-color: #fff;
}
.navigation {
  font-size: 18px;
  text-transform: uppercase;
}
.programs__article {
  background-color: #f2f2f2;
}
.programs__title {
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
}
.programs__description {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
}
.programs__link {
  font-size: 16px;
  text-transform: uppercase;
}
.how-it-works {
  background-color: #fff;
}
.how-it-works__title {
  font-size: 36px;
  font-weight: 400;
}
.how-it-works__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
}
.story {
  background-color: #f2f2f2;
}
.story__title {
  font-size: 36px;
  font-weight: 400;
}
.story__text {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
}
.story__parameter {
  font-size: 24px;
  text-transform: uppercase;
  text-align: center;
}
.story__parameter-description {
  font-size: 12px;
  color: #444444;
  text-transform: lowercase;
  text-align: center;
}
.story__result {
  font-size: 14px;
  text-transform: uppercase;
}
.presentation__note {
  font-size: 16px;
  text-transform: uppercase;
}
.contacts__invitation b {
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
}
.contacts__address {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  color: #444444;
}
.footer {
  background-color: #f2f2f2;
}
.footer__copy-name {
  font-family: "Arial", sans-serif;
  font-size: 16px;
  color: #444444;
}
.primary-link {
  box-sizing: border-box;
  display: inline-block;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-weight: 400;
  width: 100%;
  padding: 0 26px;
  background-color: #68b738;
  border: none;
  outline: 2px dotted white;
}
.primary-link--inner {
  box-sizing: border-box;
  display: inline-block;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  font-weight: 400;
  width: 100%;
  padding: 0 26px;
  background-color: #68b738;
  border: none;
  outline: 2px dotted white;
}
.main__main-information-container {
  color: #fff;
  background-color: #68b738;
}
.main__main-title {
  font-size: 36px;
  font-weight: 400;
}
.main__slogan {
  font-size: 14px;
  text-transform: uppercase;
}
.feedback__title {
  font-size: 36px;
  font-weight: 400;
}
.feedback__intro {
  font-size: 14px;
  text-transform: uppercase;
}
.feedback__note {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  text-align: center;
}
.cat-data-feedback__field-description {
  text-transform: uppercase;
  color: #444444;
}
.cat-data-feedback__field::placeholder {
  text-transform: uppercase;
  color: #444444;
}
.program-feedback__field-description {
  text-transform: uppercase;
  color: #222222;
}
.formcontact-feedback__leader {
  font-size: 24px;
}
.formcontact-feedback__field-description {
  text-transform: uppercase;
  color: #444444;
}
.formcontact-feedback__field::placeholder {
  text-transform: uppercase;
  color: #444444;
}
.more-about-cat-feedback__leader {
  font-size: 24px;
}
.more-about-cat-feedback__field::placeholder {
  text-transform: uppercase;
  color: #444444;
}
.wishes-feedback__leader {
  font-size: 24px;
}
.wishes-feedback__field-description {
  text-transform: uppercase;
}
